<template>
    <div>
      <header-component title="忘记密码"></header-component>
      <router-view></router-view>
      <div class="am-top">
        <div class="am-form-box">
          <label class="zhanghao">手机号</label>
          <div class="am-account">
            <input id="inputTel" type="number" class="txtSize"  placeholder="请输入您的手机号码" maxlength="11">
            <img @click="phoneCancel" class="am-account-cancel" src="../../assets/images/Login/Login_06.png"/>
          </div>
          <label class="zhanghao">验证码</label>
          <div class="code">
            <input id="codeInput" type="text" class="code-input"  placeholder="输入验证码" maxlength="6">
            <a @click="sendCode" v-show="show" class="send-code" id="sendCode" href="javascript:;">获取验证码</a>
            <span v-show="!show" class="count">({{count}})重新发送</span>
          </div>
          <label class="mima">设置新密码</label>
          <div class="am-passWord">
            <input id="pwdInput" type="password" class="am-pwd" placeholder="填写密码" maxlength="16">
            <img @click="pwdCancel" id="pwdCancel" class="am-password-cancel" :src="login_07"/>
            <img @click="pwdShow" class="am-password-show" src="../../assets/images/Login/Login_06.png"/>
          </div>
          <p class="am-loginBtn">
            <button @click="sureGet" type="button" class="am-btn-default">确定修改</button>
          </p>
        </div>

      </div>
    </div>
</template>

<script>
  import HeaderComponent from './header.vue'
  import login_05 from '@/assets/images/Login/Login_05.png'
  import login_07 from '@/assets/images/Login/Login_07.png'
  var urlhost = process.env.API_AJAX;
    export default {
      name:"forgetpassword",
        data() {
            return {
              aa:true,
              login_05:login_05,
              login_07:login_07,
              show: true,
              count: '',
              timer: null,
            }
        },
      mounted:function () {

        $('#inputTel').on('input', function(){
          var number = $(this).val().replace(/\s/g, '');
          if(number.length == 11){

            if(/^1[3|4|5|7|8][\d]{9}$/.test(number)){

            }else{
              this.$layer.msg("输入的手机号码有误");
              $('#inputTel').val("");
            }
          }
        });
      },
      methods:{
        phoneCancel:function () {
          $('#inputTel').val("");
        },
        pwdCancel:function () {
          if(this.aa == true){
            $("#pwdInput").attr("type","text");
            $("#pwdCancel").attr("src",this.login_05);
            this.aa=false;
          }else{
            $("#pwdInput").attr("type","password");
            $("#pwdCancel").attr("src",this.login_07);
            this.aa=true;
          }
        },
        pwdShow:function () {
          $('#pwdInput').val("");
        },
        sendCode:function () {
          var _this = this;
          var phone = $("#inputTel").val();
          $.ajax({
            type: 'POST',
            url: urlhost+"user/getcode",
            data$Type: "jsonp",
            data: {
              'phone': phone,
            },
            success: function (response) {
              var mess = JSON.parse(response);
              if(mess.status ===0){
              _this.$layer.msg(mess.message)
            }else{
                _this.$layer.msg(mess.message)
              const TIME_COUNT = 60;
              if (!_this.timer) {
                _this.count = TIME_COUNT;
                _this.show = false;
                _this.timer = setInterval(() => {
                  if (_this.count > 0 && _this.count <= TIME_COUNT) {
                    _this.count--;
                  } else {
                    _this.show = true;
                    clearInterval(_this.timer);
                    _this.timer = null;
                  }
                }, 1000)
              }
            }
            },
          });
        },
        sureGet:function () {
          var _this = this;
          var phone = $('#inputTel').val();
          var code = $('#codeInput').val();
          var password = $('#pwdInput').val();
          if(phone === '' || code === '' || password === ''){
            _this.$layer.msg("内容不能为空");
          }else{
            $.ajax({
              type: 'POST',
              url: urlhost+"user/getpass",
              data$Type: "jsonp",
              data: {
                'phone': phone,
                "password":password,
                "code":code
              },
              success: function (response) {
                var msg = JSON.parse(response);
              if(msg.status === 0){
                _this.$layer.msg(msg.message)
              }else{
                _this.$layer.msg(msg.message)
                setInterval(function () {
                  _this.$router.push({path:"/visit/login"})
                },1000)
              }
              },
            });
          }
        }

      },
        components: {
            HeaderComponent,
        }
    }
</script>

<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };



  .am-top{
    width: 90%;
    margin: auto;
    margin-top: n(120);
  }

  .am-form-box{
    width: 100%;
    margin-bottom: 0;
  }

  .am-account{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 90%;
      height: n(70);
      font-size: n(28);
      color: #b0b0b0;
    }
    img{
      width: n(50);
      height: n(50);
    }
  }

  .zhanghao{
    margin-top: n(20);
    font-weight: bold;
    display: inline-block;
    font-size: n(24);
    color: #424242;
  }
  .mima{
    @extend .zhanghao
  }

  .code{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 70%;
      height: n(70);
      font-size: n(28);
      color: #b0b0b0;
    }
    a{
      width: 25%;
      text-align: right;
      display: block;
      border-left:1px solid #f3f3f3;
      color: green;
    }
  }

  .am-form-box .am-passWord{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 70%;
      height: n(70);
      font-size: n(28);
      color: #b0b0b0;
    }
    img{
      width: n(50);
      height: n(50);
    }
  }


  .am-loginBtn{
    width: 100%;
    margin: n(80) 0 0 0;
  }

  .am-loginBtn button{
    width: 100%;
    height: n(80);
    background: #fee837;
    font-size: n(30);
    border: none;
  }






</style>



